#blogHeader {
	height: 50vh;
	background-size: cover;
	background-attachment: fixed;
	position: relative;
	overflow: hidden;
	&:after {
		content: "";
		position: absolute;
		bottom: 0px;
		left: 0;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: $triangle;
		border-color: transparent transparent transparent $mainBG;
		@include media($tablet) {
			border-width: $triangleB;
		}
	}
	&:before {
		content: "";
		position: absolute;
		bottom: 0px;
		right: 0;
		width: 0;
		height: 0;
		border-style: solid;
		border-width: $triangleRev;
		border-color: transparent $mainBG;
		@include media($tablet) {
			border-width: $triangleBRev;
		}
	}
	.header__title {
		margin: 10% 0;
		width: 50%;
		text-align:center;
		display: flex;
		flex-direction: row wrap;
		justify-content: center;
		align-items: center;
		@include media($tablet) {
			@include fill-parent();
			margin: 30% 0 10%;
		}
		&--firstName {
			display:block;
			font-family: $titleFont;
			padding-top: 2px;
			font-size: 3rem;
			line-height: 1.3;
			font-weight:300;
			position: relative;
			&:after {
				content: '';
				display: block;
				position: absolute;
				top: 0px;
				left: 0px;
				height: 1px;
				width: 25%;
				border-bottom: 1px solid black;
			}
		}
	}
}
h2.entry-title {
	position: relative;
		&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0px;
		left: 0px;
		height: 1px;
		width: 10%;
		border-bottom: 1px solid black;
	}
}
section#blogHome {
	padding: 1% 0 5%;
	position: relative;
	background: $mainBG;
	overflow:hidden;
	@include media($tablet) {
		padding: 0 0 20%;
	}
			&:before {
			content: "";
			position: absolute;
			bottom: 0px;
			right: 0;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: $triangleRev;
			border-color: transparent $alt2BG;
			@include media($tablet) {
				border-width: $triangleBRev;
			}
		}
			&:after {
			content: "";
			z-index:1;
			position: absolute;
			bottom: 0px;
			width: 0;
			left: 0;
			height: 0;
			border-style: solid;
			border-width: $triangle;
			border-color: transparent $alt2BG;
			@include media($tablet) {
				border-width: $triangleB;
			}
		}
}
.blogWrapper {
	max-width: 1080px;
	margin: 0 auto;
	display: flex;
	flex-direction: row;
	justify-content: space-between;

}
.blogHome {
	width: 80%;
}